<template>
  <div class="authorized todoProcess loadingtext"
       v-loading="imgLoading"
       element-loading-text="加载中..."
       element-loading-background="rgba(255, 255, 255, 0.8)">
    <el-container>
      <el-header>
        <div class="top-header">
          <span class="top-breadCrumb">移交设置</span>
        </div>
      </el-header>
      <el-main>
        <div class="mainContaint">
          <!--取消移交弹窗-->
          <el-dialog :visible.sync="deleteMsg" title="编辑" :close-on-click-modal="false" class="message-dialog"
                     width="30%" v-if="deleteMsg">
            <span style="text-align: left">您确认要取消移交？</span>
            <span slot="footer" class="dialog-footer">
               <el-button type="primary" @click="deleteConfirm" icon="el-icon-circle-check-outline"
                          size="mini">确 定</el-button>
               <el-button type="primary" @click="cancleConfirm" icon="el-icon-circle-close-outline"
                          size="mini">取 消</el-button>
             </span>
          </el-dialog>

          <!--筛选列表 按钮列 begin-->
          <div class="todoProcess-main">
            <el-row type="flex" justify="end" class="listButton">
              <el-button round @click="exportList" class="upload" type="primary" icon="iconfont icon-export" size="mini">导出
              </el-button>
              <el-button round type="primary" class="upload" size="mini" @click="handelAdd" icon="el-icon-plus">增加
              </el-button>
              <el-button :disabled="multipleSelection.length != 1" round type="primary" class="upload" size="mini" @click="handleEdit" icon="el-icon-edit">编辑
              </el-button>
              <el-button :disabled="multipleSelection.length != 1" round type="primary" class="upload" size="mini" @click="btnDelete" icon="el-icon-close">取消移交
              </el-button>
            </el-row>
            <!--筛选列表 按钮列 end-->

            <div class="list" :class="{table_safari:isSafari}">
              <el-table
                :height="bodyHeight - 215"
                :data="tableData"
                ref="multipleTable"
                border
                :header-row-class-name="tableHeaderClassName"
                @selection-change="handleSelectionChange"
                size="mini"
                @sort-change="sortChange">

                <el-table-column
                  type="selection"
                  width="42px">
                </el-table-column>

                <el-table-column
                  prop="fromUserName"
                  label="移交用户"
                  align="left"
                  width="180px"
                  sortable='custom'>
                  <template slot-scope="scope">
                    <div class="table-popover" :title="scope.row.fromUserName">{{ scope.row.fromUserName }}</div>
                  </template>
                </el-table-column>

                <el-table-column
                  prop="toUserName"
                  label="被移交用户"
                  align="left"
                  width="180px"
                  sortable='custom'>
                  <template slot-scope="scope">
                    <div class="table-popover" :title="scope.row.toUserName">{{ scope.row.toUserName }}</div>
                  </template>
                </el-table-column>

                <el-table-column
                  prop="description"
                  label="流程描述"
                  min-width="25px"
                  width="200px"
                  align="left">
                  <template slot-scope="scope">
                    <div class="table-popover" :title="scope.row.description">{{ scope.row.description }}</div>
                  </template>
                </el-table-column>

                <el-table-column
                  prop="operator"
                  width="180px"
                  label="操作人"
                  align="left">
                  <template slot-scope="scope">{{ scope.row.operator }}</template>
                </el-table-column>

                <el-table-column
                  prop="operationDate"
                  label="操作日期"
                  align="left"
                  width="120px"
                  sortable='custom'>
                  <template slot-scope="scope">{{scope.row.operationDate | moment}}</template>
                </el-table-column>

                <el-table-column
                  prop="processName"
                  label="移交流程"
                  align="left"
                  min-width="100px"
                  sortable='custom'>
                  <template slot-scope="scope">
                    <div class="table-popover" :title="scope.row.processName">{{ scope.row.processName }}</div>
                  </template>
                </el-table-column>
                <div slot="empty" v-show="!imgLoading">
                  <p :style="{'marginTop': '23px'}">暂无数据</p>
                </div>
                <div slot="append">
                  <!--分页-->
                  <page v-if="tableData.length > 0" :pageIndex="pageIndex" :total="total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                        @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>
                </div>
              </el-table>
            </div>

          </div>
        </div>
      </el-main>
    </el-container>

  </div>

</template>

<script>
  import {mapGetters} from "vuex";
  import util from "../../js/util";
  import loading from "../../plugins/loading/loading";
  import page from "../../plugins/page/index";

  export default {
    name: "transferSetting",
    components: {
      page: page
    },
    mounted() {
      util.burialPoint('移交设置-进入移交设置页面');
      this.selectForm.fromUserID = util.getLocalStorage("loginname"); // 移交人id
      // 获取列表数据
      this.getList();
    },
    data() {
      return {
        pageIndex: 1, //当前分页的索引
        total: 0, //数据总条数
        pageSize: 20, //当前分页显示的条数
        pageSizesList: [20, 50, 100], //分页按钮
        multipleSelection: [], //全选的所有
        deleteMsg: false, //删除弹框显示隐藏
        tableData: [], // 表格数据
        //筛选条件
        selectForm: {
          fullUserID: localStorage.getItem('loginname'), // 当前登陆人
          recType: 3, // 授权类型 3 是移交
          orderBy: "FromUserID desc" //排序
        },
        imgLoading: false // loading
      };
    },
    methods: {
      //表格排序
      sortChange(column) {
        this.selectForm.orderBy = `${
          column.prop == "recName" ? "recType" : column.prop
          } ${column.order.replace("ending", "")}`;
        this.getList();
      },
      //取消移交---弹框确定
      deleteConfirm() {
        let that = this;
        let url = "/api/deleteUserAuthority";
        let data = this.multipleSelection[0];
        this.$axios.post(url, {lotNumber: data.lotNumber}).then(result => {
          this.$message({
            type: "success",
            message: "取消移交成功!"
          });
          this.deleteMsg = false;
          this.getList();
        }).catch(err => {
            this.deleteMsg = false;
            that.$message.error(`取消移交失败!ERROR MESSAGE:${err}`);
          });
      },
      //取消移交---弹框取消
      cancleConfirm() {
        this.deleteMsg = false;
        this.$refs.multipleTable.clearSelection();
      },
      //新增按钮
      handelAdd() {
        util.burialPoint('移交设置-操作按钮-增加');
        this.$router.push({path: "/transferStyle"});
      },
      //编辑按钮
      handleEdit() {
        util.burialPoint('移交设置-操作按钮-编辑');
        let data = this.multipleSelection[0];
        this.$router.push({
          path: "/transferStyle",
          query: {
            params: {lotNumber: data.lotNumber, recType: data.recType}
          }
        });
      },
      //取消移交按钮
      btnDelete() {
        util.burialPoint('移交设置-操作按钮-取消移交');
        this.deleteMsg = true;
      },

      //获取列表
      getList() {
        this.imgLoading = true;
        let that = this;
        // 判断是否存在localStorage
        if (localStorage.authPageSize) {
          that.pageSize = Number(localStorage.authPageSize);
        }
        that.$axios.post("/api/getUserAuthorityPage?pageIndex=" + (that.pageIndex - 1) + "&pageSize=" + that.pageSize, that.selectForm)
          .then(result => {
            that.total = result.data.pageControllerInfo.totalRecordCount;
            that.tableData = result.data.userAuthorityList;
            this.imgLoading = false;
          })
          .catch(err => {
            this.imgLoading = false;
            that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
          });
      },
      // 每页显示条数
      handleSizeChange(val) {
        var pageSize = val;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        localStorage.authPageSize = val;
        this.getList();
      },
      // 当前页
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getList();
      },

      //设置表头标题class
      tableHeaderClassName({row, rowIndex}) {
        return "table-head-th";
      },

      //全选
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },

      //导出
      exportList() {
        util.burialPoint('移交设置-操作按钮-导出');
        var that = this;
        loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        var data = {
          tableName: '移交设置',
          params: that.selectForm
        }
        that.date == null ? that.date = [] : '';
        that.selectForm.beginTime = that.date[0] ? `${that.date[0]} 00:00:00` : '';
        that.selectForm.endTime = that.date[1] ? `${that.date[1]} 23:59:59` : '';
        this.$axios.post('/api/exportExcelForTable', data, {responseType: 'blob'}).then((result) => {
          const content = result.data;
          const dowLoadFileName = '移交设置列表';
          util.exportSearchList(dowLoadFileName, content);
          loading.endLoading();
        }).catch((err) => {
          loading.endLoading();
          this.$message.error(`导出Excel失败!ERROR MESSAGE:${err}`);
        });
      }
    },
    computed: {
      bodyHeight() {
        return document.documentElement.clientHeight
      },
      ...mapGetters(['isSafari', 'bodyHeight'])
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style lang="scss" scoped>
  .authorized {
    .el-date-editor.el-range-editor.el-input__inner {
      width: 82% !important;
    }

    .el-main {
      padding: 0px;
      .mainContaint {
        padding: 5px 10px 0;
        background: #ffffff;
      }
      .authorized-main {
        background: #fff;
        border-radius: 5px;
        padding: 10px 20px 20px;
        margin-top: 5px;
        .list .el-table .el-table__body .el-table__row > td .table-popover {
          min-width: 72px;
          max-width: 98%;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          color: #606266;
          cursor: default;
        }
        .listButton {
          text-align: right;
          margin-bottom: 10px;
        }
      }
      .authorized-screenList {
        background: #ffffff;
        padding: 10px 10px 0px 10px;
        border-radius: 5px;
        .todoForm {
          .el-form-item.authorized-money.el-form-item--mini {
            margin-bottom: 0 !important;
          }
        }
        .el-form-item {
          float: left;
        }
        .el-form-item.authorized-money.el-form-item--mini {
          margin-bottom: 0;
          margin-top: 0;
        }

        .el-form-item .el-select {
          width: 130%;
        }
      }
    }
  }
</style>



